<template>
  <div id="app">
    <div style="background:#f04142;height: 55px;position: relative ">
      <van-search placeholder="请输入搜索关键词" background="#f04142" shape="round" style="width: 88%;"/>
      <van-icon
          name="bianji"
          class="iconfont"
          class-prefix="icon"
          color="#fff"
          size="0.2rem"
          style="position: absolute; top:0.12rem;right:0.18rem"
      />
    </div>
    <van-tabs v-model="active" :sticky=true :offset-top=0 color="#f04142" :animated=prev duration="0.2"
              title-active-color="#f04142">
      <van-tab title="首页">
        <router-view></router-view>
      </van-tab>
      <van-tab title="精选">
        <router-view></router-view>
      </van-tab>
      <van-tab title="栏目">
        <router-view></router-view>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>


export default {
  data() {
    return {
      active: null,
      RouterUrl: ["/home", "/follow", "/column"],
      prev: false,
      sign:false
    };
  },

  mounted() {
    this.active = this.RouterUrl.indexOf(this.$router.history.current.path)
  },
  watch: {
    active: function (val) {
      this.sign? this.sign = false:this.prev ? this.$router.push(this.RouterUrl[val]) : this.prev = true
    }
  }
};
</script>

